.container {
 
    input {
        background-color: transparent;
        text-align: center;
        border: none;
        color: white;
        border-bottom: 1px solid #9e9e9e;
        border-radius: 0;
        outline: none;
        height: 3rem;
        width: 100%;
        font-size: 16px;
        margin: 0 0 8px 0;
        padding: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        -webkit-box-sizing: content-box;
        box-sizing: content-box;
        -webkit-transition: border .3s, -webkit-box-shadow .3s;
        transition: border .3s, -webkit-box-shadow .3s;
        transition: box-shadow .3s, border .3s;
        transition: box-shadow .3s, border .3s, -webkit-box-shadow .3s;
    }

    div {
        padding-top: 5px;
    }

    p {
        padding-top: 0px;
        font-size: 14px;
    }
    rect{
        fill:none;
        
    }
    text {
        fill:white;
    }
    .google-visualization-tooltip text {
        fill:black;
    }
    a{
        color: rgb(140, 147, 236)
    }
    a:visited {
        color:rgb(140, 147, 236);
    }
    a:hover {
        color: white;
    }
    h4{
        text-decoration: underline;
    }
    li{
        margin-bottom: 10px;
    }
    .repoButton {
        font-size: 20px;
        background-color: #3F51B5;
        color: white;
        border-radius: 5px;
    }
    .repoButton:hover{
        background-color: rgb(43, 55, 122);
    }

}